@import url('https://fonts.googleapis.com/css?family=Audiowide|Geostar+Fill|Londrina+Outline|Press+Start+2P|VT323');

.Home {
  text-align: center;
  height: 100%;
}

.resultsDisplayContainer {
  display: -ms-inline-flexbox;
  display: inline-flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 50px 50px 0 50px;
}

.mapSearchContanier {
  display: -ms-inline-flexbox;
  display: inline-flex;
  max-height: 30vh;
}

.resultsListContainer {
  max-height: 40vh;
  margin:0 25px 1px 25px;
  overflow-y: scroll;
  border-radius: 10px;
  border:solid 1px black;
  background: url('../images/stars.gif');
  text-align: center;
}

.ufo-logo-left {
  -webkit-animation: ufo-logo-spin-left infinite 2s alternate;
          animation: ufo-logo-spin-left infinite 2s alternate;
  height: 80px;
  margin-right: 50px;
  -webkit-filter: drop-shadow(0px 0px 1rem teal);
          filter: drop-shadow(0px 0px 1rem teal);
}

@media (max-width: 1080px) {
  .mapSearchContanier{
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }



}



